export default {
  // js
  clone: function (obj) {
    var str, newobj = obj.constructor === Array ? [] : {};
    if (typeof obj !== 'object') {
      return;
    } else if (window.JSON) {
      str = JSON.stringify(obj), //
        newobj = JSON.parse(str); //
    } else {
      for (var i in obj) {
        newobj[i] = typeof obj[i] === 'object' ?
          cloneObj(obj[i]) : obj[i];
      }
    }
    return newobj;
  },


  //boolandor,ttrue
  checkArray: function (obj) {
    var lists = ['and', 'or', 'bool', 'must', 'must_not', 'should', 'sort'];
    for (var i in lists) {
      if (obj === lists[i]) {
        return true;
      }
    }
    return false;
  },


  //
  isArray: function (obj) {
    return (typeof obj == 'object') && obj.constructor == Array;
  },


  //json
  obj_parser: function (new_obj, obj) {
    if (obj.hasOwnProperty('child')) {


      //
      if (obj['array']) {
        new_obj[obj['field']] = [];
        for (var key in obj['child']) {
          //
          if (obj['child'][key]['object']) {
            var o = {};
            var tmpSon = {};

            for (var keySon in obj['child'][key]['child']) {
              //
              if (obj['child'][key]['child'][keySon].hasOwnProperty('child')) {
                o[obj['child'][key]['child'][keySon]['field']] = this.obj_parser(tmpSon, obj['child'][key]['child'][keySon])
              }
              //
              else {
                console.log(1111)
                o[obj['child'][key]['child'][keySon]['field']] = obj['child'][key]['child'][keySon]['value']
              }
            }
            console.log(o)
            new_obj[obj['field']].push(o);
            //

            //
          } else {
            var tmp = {};
            new_obj[obj['field']].push(this.obj_parser(tmp, obj['child'][key]));
          }

        }

        //
      } else {
        var tmp = {};
        for (var key in obj['child']) {
          new_obj[obj['field']] = this.obj_parser(tmp, obj['child'][key]);
        }

      }
    } else {
      new_obj[obj['field']] = obj['value'];
      return new_obj
    }
    return new_obj
  },
  //http://www.alloyteam.com/2014/01/use-js-file-download/
  downloadFile(fileName, content){
    var aLink = document.createElement('a');
    var blob = new Blob([content]);
    var evt = document.createEvent("HTMLEvents");
    evt.initEvent("click", false, false);
    aLink.download = fileName;
    aLink.href = URL.createObjectURL(blob);
    aLink.dispatchEvent(evt);
  },
  word(conf){
    var config=conf?conf:{
        type:'inline',
        lang:['en','cn']
    };
    function inlinTranse(el,lang){
      var word = el.getAttribute('word');
      el.setAttribute('word',el.innerHTML);
      el.setAttribute('word-l',lang);
      el.innerHTML=word;
    }
    function inlineWrap(el,lang){
      //
      if(lang!==config.lang[0]) {
        if (!el.getAttribute('world-l')) {
          inlinTranse(el, lang);
        } else if (el.getAttribute('world-l') !== lang) {
          inlinTranse(el, lang);
        }
      }else if(lang===config.lang[0]&&el.getAttribute('world-l')!==lang){
          inlinTranse(el, lang);
        }
    }
    function change(lang){
      if(config.lang.indexOf(lang)===-1){
        throw 'no language in config';
      }
      var els=document.querySelectorAll("[word]");
      for(var i=0;i<els.length;i++){
        inlineWrap(els[i],lang);
      }
    }
  }
}







